  box-shadow: 3px 4px 4px #132228, -6px -8px 12px #274652, 6px 8px 16px #132228;
}

.pushed2pulled:after {
  opacity: 1;
  box-shadow: inset 3px 4px 4px #132228, inset -6px -8px 12px #274652, inset 6px 8px 16px #132228;
}

.pushed2pulled.active:before, .pushed2pulled:hover:before {
  opacity: 1;
}

.pushed2pulled.active:after, .pushed2pulled:hover:after {
  opacity: 0;
}

.pulled2pushed:before, .pulled2pushed:after {
  transition: opacity .3s cubic-bezier(.175, .885, .32, 1.275);
}

.pulled2pushed:before {
  opacity: 1;
  box-shadow: 3px 4px 4px #132228, -6px -8px 12px #274652, 6px 8px 16px #132228;
}

.pulled2pushed:after {
  opacity: 0;
  box-shadow: inset 3px 4px 4px #132228, inset -6px -8px 12px #274652, inset 6px 8px 16px #132228;
}

.pulled2pushed.active:before, .pulled2pushed:hover:before, .pulled2pushed.active:after, .pulled2pushed:hover:after {
  opacity: 1;
