  z-index: 1;
  vertical-align: middle;
  scroll-snap-stop: always;
  scroll-snap-align: center;
  white-space: normal;
  border-radius: 10px;
  flex-direction: column;
  justify-content: space-between;
  width: 90%;
  height: 100%;
  margin: 0 2%;
  padding: .5em 1em;
  display: inline-flex;
  position: relative;
  box-shadow: 0 8px 10px 1px #0000001a, 0 3px 14px 2px #0003, 0 4px 15px #00000054;
}

.slider .slide:first-child {
  margin-left: 4%;
}

.slider .slide:last-child {
  margin-right: 4%;
}

.slider .slide-content-container {
  flex-direction: column;
  flex-grow: 1;
  justify-content: center;
  align-items: stretch;
  min-height: 0;
  display: flex;
}

