.slider .slider-control {
  --size: 33px;
  z-index: 3;
  width: var(--size);
  height: var(--size);
  background: var(--color-bg);
  cursor: pointer;
  opacity: 1;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: opacity .3s cubic-bezier(.215, .61, .355, 1);
  display: flex;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  box-shadow: 0 10px 13px 2px #0000001a, 0 4px 16px 3px #0003, 0 5px 14px #00000054;
}

.slider .slider-control:after {
  content: "";
  border-bottom: 1px solid #fff;
  border-left: 1px solid #fff;
  width: 8px;
  height: 8px;
}

.slider .slider-control.prev {
  left: calc(var(--size) / 3 * -1);
}

.slider .slider-control.prev:after {
  transform: translateX(25%)rotate(45deg);
}

.slider .slider-control.next {
  right: calc(var(--size) / 3 * -1);
}

.slider .slider-control.next:after {
  transform: translateX(-25%)rotate(-135deg);
}

.slider .slider-control.hidden {
  pointer-events: none;
  opacity: 0;
}

.slider-fullscreen {
